<template>
    <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
            <v-col cols="12" xs="12" sm="10" md="8">

                <v-card elevation="8">

                    <v-toolbar color="#F8D117" elevation="1">
                        <v-card-title>Dados da Operação</v-card-title>
                        <v-spacer />
                        <v-btn
                            icon 
                            @click="simularEmprestimo"
                            :disabled="!validForm"
                        >
                            <v-icon>mdi-calculator</v-icon>
                        </v-btn>
                        <v-btn
                            icon
                            @click="limparCampos"
                        >
                            <v-icon>mdi-trash-can-outline</v-icon>
                        </v-btn>
                    </v-toolbar>

                    <v-card-text>
                        <v-form
                            ref="formDados"
                            v-model="validForm"
                        >
                            <v-row justify="space-around">
                                <v-col cols="12" xs="12" sm="6" md="3">
                                    <v-text-field
                                        label="Valor"
                                        v-model.lazy="emprestimo.valor"
                                        v-money="moneyConfig"
                                        prefix="R$"
                                        manual-clean
                                        ref="inputValor"
                                        @keydown="keyHandler"
                                    />
                                </v-col>
                                <v-col cols="12" xs="12" sm="6" md="3">
                                    <v-text-field
                                        label="Tarifa"
                                        v-model.lazy="emprestimo.tac"
                                        :append-icon="emprestimo.tacPercent ? 'mdi-percent' : 'mdi-cash-usd'"
                                        @click:append="toggleTacPercent"
                                        @keydown="keyHandler"
                                        v-money="emprestimo.tacPercent ? percentConfigPrecision2 : moneyConfig"
                                        :prefix="emprestimo.tacPercent ? '' : 'R$'"
                                        manual-clean
                                        ref="inputTac"
                                    />
                                </v-col>
                                <v-col cols="12" xs="12" sm="6" md="3">
                                    <v-text-field
                                        label="Comissão Flat"
                                        v-model.lazy="emprestimo.flat"
                                        :append-icon="emprestimo.flatPercent ? 'mdi-percent' : 'mdi-cash-usd'"
                                        @click:append="toggleFlatPercent"
                                        @keydown="keyHandler"
                                        v-money="emprestimo.flatPercent ? percentConfigPrecision2 : moneyConfig"
                                        :prefix="emprestimo.flatPercent ? '' : 'R$'"
                                        manual-clean
                                        ref="inputFlat"
                                    />
                                </v-col>
                            </v-row>

                            <v-row justify="space-around">
                                <v-col cols="12" xs="12" sm="6" md="3">
                                    <v-text-field
                                        label="Dia base"
                                        v-model.number="emprestimo.diaBase"
                                        v-mask="'##'"
                                        :rules="[rules.checkDiaBase]"
                                    />
                                </v-col>
                                <v-col cols="12" xs="12" sm="6" md="3">
                                    <v-text-field
                                        label="Taxa de Juros"
                                        v-model.lazy="emprestimo.taxa"
                                        v-money="percentConfigPrecision3"
                                        manual-clean
                                        ref="inputTaxa"
                                        @keydown="keyHandler"
                                    />
                                </v-col>
                                <v-col cols="12" xs="12" sm="6" md="3">
                                    <v-menu
                                        v-model="menuData"
                                        :close-on-content-click="false"
                                        :nudge-right="40"
                                        transition="scale-transition"
                                        offset-y
                                        min-width="290px"
                                    >
                                        <template v-slot:activator="{ on }">
                                            <v-text-field
                                                label="Data da Liberação"
                                                append-icon="mdi-calendar"
                                                readonly
                                                v-on="on"
                                                :value="dataLiberacaoFormatted"
                                            />
                                        </template>
                                        <v-date-picker
                                            v-model="emprestimo.dataLiberacao"
                                            @input="menuData = false"
                                            locale="pt-br"
                                        />
                                    </v-menu>
                                </v-col>
                            </v-row>

                            <v-row justify="space-around">
                                <v-col cols="12" xs="12" sm="6" md="3">
                                    <v-text-field
                                        label="Qtde de Parcelas (Meses)"
                                        v-model.number="emprestimo.qtdeParcelas"
                                        v-mask="'##'"
                                        :rules="[rules.positiveOnly]"
                                    />
                                </v-col>
                                <v-col cols="12" xs="12" sm="6" md="3">
                                    <v-text-field
                                        label="Carência (Meses)"
                                        v-model.number="emprestimo.carencia"
                                        v-mask="'#'"
                                        :rules="[rules.positiveAndZero]"
                                    />
                                </v-col>
                                <v-col cols="12" xs="12" sm="6" md="3">
                                    <!-- Elemento vazio -->
                                </v-col>
                            </v-row>

                            <v-divider class="mb-4" />

                            <v-row class="d-print-none">
                                <v-col class="d-flex justify-end">
                                    <v-btn
                                        color="#23569C"
                                        class="white--text"
                                        large                                                                
                                        @click="simularEmprestimo"
                                        elevation="8"
                                        :disabled="!validForm"
                                    >
                                        <v-icon>mdi-calculator</v-icon>
                                        <span class="mx-2">Simular</span>
                                    </v-btn>
                                    <v-btn
                                        color="warning"                                        
                                        class="ml-2"
                                        large                                        
                                        @click="limparCampos"
                                        elevation="8"                                        
                                    >
                                        <v-icon>mdi-trash-can-outline</v-icon>
                                        <span class="mx-2">Limpar</span>
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </v-form>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { mask } from 'vue-the-mask'
import { VMoney } from 'v-money'
import moment from 'moment'
import { strToFloat } from '@/params'

// Componente Vue
export default {
    name: 'Dados',

    directives: {
        mask,
        money: VMoney
    },
    // lifecycle mounted
    mounted() {
        this.$refs.inputValor.focus()
    },
    data: () => ({
        menuData: false,    // date picker menu (controla a exibição do menu da data)
        validForm: false,

        // regras de validação de inputs
        rules: {
            positiveOnly: value => +value > 0 || 'Valor deve ser maior que zero.',
            positiveAndZero: value => +value >= 0 || 'Valor deve ser maior ou igual a zero.',
            checkDiaBase: value => value >= 1 && value <= 28 || 'Dia base para prestação deve estar entre 1 e 28.',
            checkMaskedValue: value => {
                let aux = parseFloat(value.replace('%', '').replace(/\./g, '').replace(',', '.'))
                return aux > 0 || 'Valor deve ser maior que zero.'
            }
        },

        // configuração da máscara de dinheiro
        moneyConfig: {
            decimal: ",",
            thousands: ".",
            precision: 2,
            masked: false // não funciona com diretivas
        },
        // configuração da máscara percentual, com 3 casas decimais
        percentConfigPrecision3: {
            decimal: ",",
            thousands: ".",
            prefix: "",
            suffix: "%",
            precision: 3,
            masked: false
        },
        // configuração da máscara percentual, com 2 casas decimais
        percentConfigPrecision2: {
            decimal: ",",
            thousands: ".",
            prefix: "",
            suffix: "%",
            precision: 2,
            masked: false
        },
    }),
    methods: {
        ...mapActions(['setEmprestimo', 'resetEmprestimo', 'setResultado', 'setIsEditing']),
        // evento genérico para tratar teclas específicas
        keyHandler(event) {
            if (event.key === '-') {
                event.preventDefault()
            }
        },        
        toggleFlatPercent() {
            this.emprestimo.flatPercent = !this.emprestimo.flatPercent
            this.$refs.inputFlat.focus()
        },
        toggleTacPercent() {
            this.emprestimo.tacPercent = !this.emprestimo.tacPercent
            this.$refs.inputTac.focus()
        },
        limparCampos() {
            // Há  um issue no v-money que impede alterações dos valores via código
            // inclusive para limpar. Então é necessário limpar os elementos na DOM
            // e em seguida zerar os valores nos atributos, pois o v-model não fun-
            // ciona.
            const elements = document.querySelectorAll("[manual-clean]");
            for (let i = 0; i < elements.length; i++) {
                elements[i].value = 0;
            }

            this.resetEmprestimo() // retorna o store aos valores iniciais
            
            this.$refs.inputValor.focus()

        },
        simularEmprestimo() {
            const valor = strToFloat(this.emprestimo.valor)
            const taxa = strToFloat(this.emprestimo.taxa)
            if (valor && taxa) {
                this.setEmprestimo(this.emprestimo) // atualiza o state do módulo dados que, por sua vez, atualiza o módulo simulação
                this.setIsEditing(false)
            } else {
                valor ? this.$refs.inputTaxa.focus() : this.$refs.inputValor.focus()                            
            }
        }
    },
    computed: {
        ...mapGetters({
            emprestimo: 'getEmprestimo',
        }),
        dataLiberacaoFormatted() {
            return this.emprestimo.dataLiberacao
                ? moment(this.emprestimo.dataLiberacao).format('DD/MM/YYYY')
                : ''
        }
    }
};
</script>

